import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import { library } from '@fortawesome/fontawesome-svg-core'
import { faCoffee, faCamera, faLaptop, faCheck, faHospital, 
  faPlane, faQuestion, faBan, faBars, faPlus, faSave, faTrash,
  faSignOutAlt, faSearch, faLock, faBuilding, faCity, faTimes,
  faCalendarAlt, faArrowRight, faArrowDown, faHammer, faUser, faKey, faClock, faCalendar, faCheckCircle
} from '@fortawesome/free-solid-svg-icons'
library.add(faCoffee, faCamera,faLaptop, faCheck, faHospital, 
  faPlane, faQuestion, faBan, faBars, faPlus, faSave, faTrash,
  faSignOutAlt, faSearch, faLock, faBuilding, faCity,faTimes,
  faCalendarAlt, faArrowRight, faArrowDown,faHammer, faUser, faKey, faClock, faCalendar, faCheckCircle
)

import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
Vue.component('font-awesome-icon', FontAwesomeIcon)

import Mint from 'mint-ui'
import 'mint-ui/lib/style.min.css'
Vue.use(Mint);

import helper from './plugins'
Vue.use(helper)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
